import { useState, useEffect } from 'react'
import { animated, useSpring } from 'react-spring'
import * as easings from 'd3-ease'
import PageNav from './pageNav'

const Item = ({itemType, itemIndex, setPrev, fontSize, page}) => {

    const ProjectName = ["Dummy One", "Dummy Two", "Dummy Three", "Dummy Four"]
    const WorkName = ["Dummy Work One", "Dummy Work Two", "Dummy Work Three", "Dummy Work Four"]

    var header = ""

    if(itemType === "Projects"){
        header = ProjectName[itemIndex]
    } else {
        header = WorkName[itemIndex]
    }


    const SectionAnimate = useSpring({
        from: {
            transform: 'translateX(-100vw)',
            opacity: 0
        },
        to: {
            transform: (page === "Item")?'translateX(0vw)':'translateX(-100vw)',
            opacity: (page === "Item")?1:0
        },
        delay: 150,
        config: {
            duration: 1200,
            easing: easings.easeCubic
        }
    })

    return (
        <animated.div className="item-page" style={SectionAnimate}>            
            <PageNav prevPage={setPrev} page={page} pageName={"Item"}/>
            <div className="item-details">
                <div className="header">
                    <h1>{header}</h1>
                    <h2>Roles</h2>
                    <ul>
                        <li>Dummy Role</li>
                        <li>Dummy Role</li>
                        <li>Dummy Role</li>
                    </ul>
                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum</p>
                </div>
            </div>
        </animated.div>
    )
}

export default Item